$nav-height: 50px;
$space: 12px;

/* Colors from Discord */

$color-1: #202225;
$color-2: #2f3136;
$color-3: #36393f;
$color-4: #40444b;

.app {
  height: 100%;
  width: 100%;

  .header {
    background-color: $color-1;
    justify-content: space-between;
    height: $nav-height;
    width: 100%;

    .left {
      align-items: center;
      padding-left: $space;
      color: #ccc;
      width: 208px;
    }

    .center {
      .nav {
        .nav-button {
          background-color: $color-2;
          height: 100%;

          .nav-link {
            width: 100%;
            height: 100%;
            padding: 0px 18px;
            align-items: center;
            text-decoration: none;
            transition: all 0.1s ease-in-out;
            color: white;
          }

          .nav-link:hover {
            background-color: $color-4;
          }

          .nav-selected {
            background-color: orange;
          }
        }
      }
    }

    .right {
      width: 208px;
      justify-content: flex-end;
      align-items: center;
      padding-right: $space;
      color: #ccc;

      .header-link-button {
        height: 26px;
        width: 26px;
        cursor: pointer;
      }

      .header-logo {
        height: 100%;
        width: 100%;
      }
    }
  }

  .content {
    background-color: $color-3;
    height: calc(100% - $nav-height);
    overflow-y: auto;
  }
}

@media screen and (max-width: 725px) {
  .app {
    .header {
      justify-content: center;

      .left {
        display: none;
      }

      .center {
      }

      .right {
        display: none;
      }
    }
  }
}
